@use "~ui/library";

.authInput {
    @include library.medium;
    padding: library.$x * 2;
    color: library.$c12;
    background: library.$c3;
    border-radius: library.$border-radius library.$border-radius 3*library.$border-radius 3*library.$border-radius;
    transition: color 0.2s;
    transition: background 0.2s;
    &:focus {
        background: library.$c11;
    }
    &::placeholder {
        @include library.thinFontWeight;
        color: rgba(library.$c12, 1);
    }
}

