@use '~ui/library';

.authFormButton {
    @include library.medium;
    width: 100%;
    height: auto;
    box-shadow: 0 3 * library.$x library.$c1;
    min-height: library.$x * 16;
    padding: library.$x * 2;
    color: library.$c1;
    background: library.$c13;
    border-radius: library.$border-radius library.$border-radius 4*library.$border-radius 4*library.$border-radius;
    transition: color 0.2s;
    transition: background 0.2s;
    &:hover, &:focus {
        background: library.$c11;
        color: library.$c13;
        
    }
}