@use '~ui/library';

.authPage {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    align-items: center;
}

.appName {
    @include library.asset;
    width: 100%;    
    top: 100%;
    left: 50%;
    transform: translate(-50%, -88%);
    z-index: 1;
}

.appNameBackground {
    position: relative;
    width: 100%;
    height: library.$x * 120;
    background: library.$c11;
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: center;
}

.notes {
    @include library.asset;
    top: 0;
    left: 0;
}

.notesWrapper {
    position: absolute;
    left:0;
    bottom: 25%;
    overflow: hidden;
    width: 100%;
    z-index: -1;
}

.fakeNotes {
    visibility: hidden;
}

.contentContainer {
    position: relative;
    width: 100%;
    height: auto;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;    
}

.conent {
    @include library.medium;
    width: 32.5%;
    margin: library.$x * 6 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: library.$c12;
}

.authPageFooter {
    margin: library.$x * 6 auto;
}

@mixin container {
    display: flex;
    align-items: end;
}

.peopleContainer {
    @include library.asset;
    width: 100%;
    display: flex;
    justify-content: space-between;
    bottom: 0;
    overflow: hidden;
    z-index: 1;
}

.manContainer {
    @include container;
    bottom: 0;
    left: 0;    
}

.womanContainer {
    @include container;
    bottom: 0;
    right: 0;
}

.person {
    height: library.$x * 170;
}

* {
    shape-rendering: geometricPrecision;
}

.micro {
    margin-left: library.$x * 6;
    height: library.$x * 100;
    
    outline: none;
    border: none;
}

@media screen and (orientation: portrait) {
    .peopleContainer {
        display: none;
    }

    .conent {
        width: 55%;
        margin: library.$x * 12 auto;
    }
    
  }