@use "sass:map";
@import url('https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

$x: 0.35vh;

$c10: #726099;
$c11: #645486;
$c12: #FFF6F0;
$c13: #FFB55F;
$c1: #36323F;
$c1p5: #393542;
$c2: #3C3846;
$c3: #524D61;
$c4: #454251;
$c4a85: rgba($c4, .85);

$status: (
    "online": #0D9A1B,
    "deserted": #F9C339,
    "talking": #F93939,
    "offline": #2A233F,
);

$border-radius: $x;

@mixin defaultFont {
    font-family: 'Jost' sans-serif;
}

@mixin thinFontWeight {
    font-weight: 300;
}

@mixin defaultFontWeight {
    font-weight: 400;
}

@mixin thikFontWeight {
    font-weight: 600;
}

@mixin large {
    @include defaultFont;
    font-size: 16 * $x;
}

@mixin titles {
    @include defaultFont;
    font-size: 10 * $x;
}

@mixin extremely-large {
    @include defaultFont;
    font-size: 60 * $x;
}

@mixin medium {
    @include defaultFont;
    font-size: 7.75 * $x;
}

@mixin small {
    @include defaultFont;
    font-size: 6.5 * $x;
}

@mixin tiny {
    @include defaultFont;
    font-size: 5 * $x;
}

@mixin chat {
    @include defaultFont;
    font-size: 4 * $x;
}

@mixin asset {
    position: absolute;
    pointer-events: none;
}