@use '~ui/library';

.signUpForm {
    width: 100%;
    min-width: fit-content;
    @include library.thinFontWeight;
    display: flex;
    flex-direction: column;
    gap: library.$x * 6;
}

.formController {
    width: 75%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: library.$x * 6;
    align-self: center;
}